let timer = null;

export default {
  bind(el, { value }) {
    const result = timestampFormat(value);
    const isInterval =
      result === "" ||
      result.indexOf("") !== -1 ||
      result.indexOf("") !== -1;
    el.innerText = result;
    if (isInterval) {
      //
      timer = setInterval(() => {
        const result = timestampFormat(value);
        el.innerText = result;
      }, 1000 * 60 * 5);
    }
  },
  update(el, { value }) {
    const result = timestampFormat(value);
    const isInterval =
      result === "" ||
      result.indexOf("") !== -1 ||
      result.indexOf("") !== -1;
    el.innerText = result;
    if (isInterval) {
      //
      timer = setInterval(() => {
        const result = timestampFormat(value);
        el.innerText = result;
      }, 1000 * 60 * 5);
    }
  },
  unbind() {
    clearInterval(timer);
  }
};
function zeroize(num) {
  return (String(num).length == 1 ? "0" : "") + num;
}

function timestampFormat(timestamp) {
  let curTimestamp = parseInt(new Date().getTime() / 1000); //
  let timestampDiff = curTimestamp - timestamp; // 
  let curDate = new Date(curTimestamp * 1000); // 
  let tmDate = new Date(timestamp * 1000); // 

  let Y = tmDate.getFullYear(),
    m = tmDate.getMonth() + 1,
    d = tmDate.getDate();
  let H = tmDate.getHours(),
    i = tmDate.getMinutes(),
    s = tmDate.getSeconds();

  if (timestampDiff < 60) {
    return "";
  } else if (timestampDiff < 3600) {
    // 
    return Math.floor(timestampDiff / 60) + "";
  } else if (
    // curDate.getFullYear() == Y &&
    // curDate.getMonth() + 1 == m &&
    // curDate.getDate() == d
    timestampDiff <
    60 * 60 * 24
  ) {
    return Math.floor(timestampDiff / (60 * 60)) + "";
    // return '' + zeroize(H) + ':' + zeroize(i)
  } else {
    // let newDate = new Date((curTimestamp - 86400) * 1000); // 
    if (
      // newDate.getFullYear() == Y &&
      // newDate.getMonth() + 1 == m &&
      // newDate.getDate() == d
      60 * 60 * 24 < timestampDiff &&
      timestampDiff < 60 * 60 * 24 * 2
    ) {
      //return '' + zeroize(H) + ':' + zeroize(i)
      return "";
    } else if (curDate.getFullYear() == Y) {
      return (
        zeroize(m) + "-" + zeroize(d) + " " + zeroize(H) + ":" + zeroize(i)
      );
    } else {
      return (
        Y + "-" + zeroize(m) + "-" + zeroize(d)
        // + "- "
        // zeroize(H) +
        // ":" +
        // zeroize(i)
      );
    }
  }
}
