import { isFunc } from "./type";
import { warn } from "./log";
// 
export const dateFormate = (
  _date = new Date(),
  fmt = "yyyy-MM-dd hh:mm:ss"
) => {
  const date = new Date(_date);
  const o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "h+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    S: date.getMilliseconds() //
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};

// 
export function debounce(func, delay = 300, I = null) {
  return () => {
    clearTimeout(I);
    I = setTimeout((...args) => func(...args), delay);
  };
}
//
export function flatten(arr) {
  return [].concat(...arr.map(_ => (Array.isArray(_) ? flatten(_) : _)));
}

// 
//
export function throttle(func, delay = 60) {
  let lock = false;
  return (...args) => {
    if (lock) return;
    func(...args);
    lock = true;
    setTimeout(() => (lock = false), delay);
  };
}

//  
//  curry(foo) , g4
//  foo(a,b,c,d)
//  
export const curry = func => {
  const g = (...allArgs) =>
    allArgs.length >= func.length
      ? func(...allArgs)
      : (...args) => g(...allArgs, ...args);
  return g;
};

export async function handlerPromise(func) {
  try {
    const data = await func();
    return [data, null];
  } catch (err) {
    return [null, true];
  }
}

// 
export function once(func) {
  let called = false;
  return function() {
    if (!called) {
      called = true;
      func.apply(this, arguments);
    }
  };
}

// 
export function orientate(func1, func2) {
  if (!isFunc(func1) || !isFunc(func2)) {
    warn("orientate 2 ");
  }
  window.addEventListener(
    "onorientationchange" in window ? "orientationchange" : "resize",
    () => {
      if (window.orientation === 180 || window.orientation === 0) {
        // "
        func1();
        return;
      }
      if (window.orientation === 90 || window.orientation === -90) {
        // "
        func2();
        return;
      }
    },
    false
  );
}

// 
export function isMobile() {
  return /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent);
}

// 
export function isMatches() {
  const mql = window.matchMedia("(orientation: portrait)");
  return !mql.matches;
}

export function getScrollTop() {
  return document.documentElement.scrollTop || document.body.scrollTop;
}

export function generateUUID() {
  let d = new Date().getTime();
  if (window.performance && typeof window.performance.now === "function") {
    d += performance.now(); // use high-precision timer if available
  }
  const uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
    c
  ) {
    const r = (d + Math.random() * 16) % 16 | 0;
    d = Math.floor(d / 16);
    return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
  });
  return uuid;
}
