import { generateUUID } from "./tools";

export function fileToBase64(file) {
  const reader = new FileReader();
  reader.readAsDataURL(file);
  return new Promise(resolve => {
    reader.onloadend = function() {
      //  base64 ,  img  src 
      resolve(reader.result);
    };
  });
}

export async function compressedImg(file) {
  const dataBase64Url = await fileToBase64(file);
  const result = await dealImage(dataBase64Url, {}, file.type);
  return dataURLtoFile(result, generateUUID());
}

export function dataURLtoFile(dataurl, filename) {
  // base64
  const arr = dataurl.split(",");
  const mime = arr[0].match(/:(.*?);/)[1];
  const bstr = atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
}

/**
 * 
 * @param {Object} path
 *   pc
 * @param {Object} obj
 *   obj   width height quality(0-1)
 * @param {Object} callback
 *   base64
 */
export function dealImage(path, obj = {}, formatType = "image/jpeg") {
  const img = new Image();
  img.src = path;
  return new Promise(resolve => {
    img.onload = function $_() {
      const that = this;
      // 
      let w = that.width;
      let h = that.height;
      const scale = w / h;
      w = obj.width || w;
      h = obj.height || w / scale;
      let quality = 0.7; // 0.7
      // canvas
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      // 
      const anw = document.createAttribute("width");
      anw.nodeValue = w;
      const anh = document.createAttribute("height");
      anh.nodeValue = h;
      canvas.setAttributeNode(anw);
      canvas.setAttributeNode(anh);
      ctx.drawImage(that, 0, 0, w, h);
      // 
      if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
        quality = obj.quality;
      }
      // quality
      const base64 = canvas.toDataURL(formatType, quality);
      // base64
      resolve(base64);
    };
  });
}
// 
export function preLoadImg(src, cb) {
  const Img = new Image();
  Img.onload = cb;
  Img.src = src;
}
